/**
 *  Common query model
 */
export default class Query {
  /**
   * Query constructor
   * @param rootQuery root query path (optional)
   * @param separator query parameters separator (like '&' or )
   * @param {[QueryParameter|StaticParameter]} parameters query parameters (optional) as Query parameters
   */
  constructor(rootQuery, separator, parameters = []) {
    this.rootQuery = rootQuery
    this.separator = separator
    this.parameters = parameters
  }

  /**
   * Search parameter by name in query
   * @param {string} parameterName  searched parameter name
   * @return parameter when found, undefined otherwise
   */
  getParameter(parameterName) {
    return this.parameters.find((p) => p.name === parameterName)
  }

  /**
   * @return {string} query string
   */
  toQueryString() {
    const parametersText = this.parameters.map((p) => p.toQueryString()).filter((p) => !!p).join(this.separator)
    return `${this.rootQuery || ''}${this.rootQuery && parametersText ? this.separator : ''}${parametersText}`
  }
}
