function displayNumber(number, nbDecimal) {
  let ret = number
  if (number >= 1000000) {
    ret = `${parseFloat((number / 1000000).toFixed(nbDecimal))}M`
  } else if (number >= 1000) {
    ret = `${parseFloat((number / 1000).toFixed(nbDecimal))}k`
  }
  return ret
}

export default {
  displayNumber,
}
