/**
 * Validates a JSON objects
 * @author Raphal Mechali
 */

/** Recursive JSON definition */
export const JSONObject = PropTypes.objectOf(
  PropTypes.oneOfType([
    // Simple values
    PropTypes.number,
    PropTypes.string,
    PropTypes.bool,
    // recursive structure cannot be implemented in proptypes
    PropTypes.object,
    PropTypes.array,
  ]),
)
