import { Schema, arrayOf } from 'normalizr'

export const ServiceProviderConfiguration = {
  entityKey: 'name',
  normalizrKey: 'serviceproviders',
}

export const SERVICE_PROVIDER = new Schema(ServiceProviderConfiguration.normalizrKey, {
  idAttribute: (serviceProvider) => serviceProvider.content[ServiceProviderConfiguration.entityKey],
})

export const SERVICE_PROVIDER_ARRAY = arrayOf(SERVICE_PROVIDER)
